import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: "/",
			name: "lucky",
			component: () => import("../views/lucky.vue"),
			meta: {
				menuPage: true,
			},
		},
	]
})



